import React from "react";
import PropTypes from "prop-types";
import cn from "classnames";
import styled from "astroturf"
import { Link as GatsbyLink } from "gatsby";

import { ReactComponent as ArrowNext } from "../../assets/img/icons/arrow-next-more.svg";

const NativeLink = styled("a", null, "NativeLink", require("./GoNextLink-NativeLink.module.scss"), "native-link", "nativeLink");

const styles = require('./GoNextLink-styles.module.scss');

export const GoNextLink = ({
  children,
  withArrow = true,
  gatsby = false,
  className,
  big = false,
  white = false,
  ...props
}) => {
  const Container = gatsby ? GatsbyLink : NativeLink;

  return (
    <Container
      className={cn(className, styles.goNextLink, { [styles.big]: big, [styles.white]: white })}
      {...props}
    >
      {children}
      {withArrow && <ArrowNext />}
    </Container>
  );
};

GoNextLink.propTypes = {
  withArrow: PropTypes.bool,
  gatsby: PropTypes.bool,
  className: PropTypes.string,
  big: PropTypes.bool,
  white: PropTypes.bool,
};
